@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

html {
  background-color: #e7e5e5;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  width: 100%;
  min-height: 100%;
  margin: 0 auto;
  font-style: normal;
  font-family: "PT Sans", "Areal", sans-serif;
  text-align: center;
  color: #000000;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
@media (min-width: 768px) {
  body {
    max-width: 768px;
  }
}
@media (min-width: 1200px) {
  body {
    max-width: 1200px;
  }
}

img {
  display: block;
  max-width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

.logo {
  position: absolute;
  left: 50%;
  z-index: 4;
  display: block;
  width: 185px;
  height: 96px;
  transform: translateX(-50%);
  cursor: pointer;
}
@media (min-width: 768px) {
  .logo {
    z-index: 7;
  }
}

.welcome {
  position: relative;
  padding-top: 90px;
  background-color: #81b3d2;
  background-image: url("../img/jpg/background-mobile.jpg");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .welcome {
    background-image: url("../img/jpg/background-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .welcome {
    padding-top: 77px;
    background-image: url("../img/jpg/background-tablet.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .welcome {
    background-image: url("../img/jpg/background-tablet@2x.jpg");
  }
}
@media (min-width: 1200px) {
  .welcome {
    padding-top: 78px;
    background-image: url("../img/jpg/background-desktop.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .welcome {
    background-image: url("../img/jpg/background-desktop@2x.jpg");
  }
}
.welcome--inner {
  padding-top: 52px;
}
@media (min-width: 768px) {
  .welcome--inner {
    padding-top: 95px;
  }
}
@media (min-width: 1200px) {
  .welcome--inner {
    padding-top: 101px;
  }
}
.welcome::after {
  content: " ";
  position: initial;
  display: block;
  width: 100%;
  height: 57px;
  background-color: transparent;
  background-image: url("../img/svg/decor-element.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.welcome__img {
  display: block;
  box-sizing: border-box;
  margin: 0 auto;
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .welcome__img {
    width: 461px;
    height: 350px;
    margin-bottom: 34px;
  }
}
@media (min-width: 1200px) {
  .welcome__img {
    width: 459px;
    margin-bottom: 33px;
  }
}
.welcome__img--inner {
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .welcome__img--inner {
    width: 457px;
    height: 78px;
    margin-bottom: 60px;
  }
}
@media (min-width: 1200px) {
  .welcome__img--inner {
    width: 459px;
    height: 79px;
    margin-bottom: 63px;
  }
}

.introduction__title {
  max-width: 440px;
  margin: 0 auto;
  margin-bottom: 35px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .introduction__title {
    font-size: 21px;
    margin-bottom: 29px;
  }
}
.introduction__title--big-title {
  font-size: 21px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .introduction__title--big-title {
    font-size: 30px;
    margin-bottom: 34px;
  }
}
.introduction__title--catalog {
  margin-bottom: 26px;
}
@media (min-width: 768px) {
  .introduction__title--catalog {
    font-size: 30px;
    margin-bottom: 32px;
  }
}
@media (min-width: 1200px) {
  .introduction__title--catalog {
    margin-bottom: 32px;
  }
}
.introduction__title--form {
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .introduction__title--form {
    font-size: 30px;
    margin-bottom: 22px;
  }
}
@media (min-width: 1200px) {
  .introduction__title--form {
    margin-bottom: 32px;
  }
}

.introduction__text {
  max-width: 446px;
  margin: 0 auto;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .introduction__text--long {
    max-width: 485px;
  }
}
@media (min-width: 768px) {
  .introduction__text--small {
    max-width: 435px;
  }
}
.introduction__text br {
  display: none;
}
@media (min-width: 768px) {
  .introduction__text br {
    display: block;
  }
}

.main-header {
  display: block;
  min-height: 56px;
}
@media (min-width: 768px) {
  .main-header {
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .main-header {
    margin: 0 auto;
    padding: 0;
  }
}

.no-js {
  position: static;
}
.no-js .toggle {
  display: none;
}
.no-js .navigation__list {
  display: block;
}
@media (min-width: 768px) {
  .no-js .navigation__list {
    display: grid;
    font-size: 14px;
    line-height: 21px;
    box-shadow: none;
    grid-template-columns: repeat(5, 1fr);
  }
}

.is-open .navigation__list {
  position: absolute;
  z-index: 5;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .is-open .navigation__list {
    position: relative;
    display: grid;
  }
}

.toggle {
  position: absolute;
  right: 25px;
  display: block;
  width: 23px;
  height: 22px;
  margin: 0 auto;
  margin-top: 17px;
  margin-bottom: 17px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 6;
}
.toggle:hover .toggle__svg {
  fill: #669ec0;
}
.toggle:active .toggle__svg {
  opacity: 0.6;
}
.toggle:hover .toggle__svg-close {
  fill: rgba(255, 255, 255, 0.6);
}
.toggle:active .toggle__svg-close {
  fill: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .toggle {
    display: none;
  }
}

.toggle__svg {
  fill: #81b3d2;
}

.toggle__svg-close {
  fill: #ffffff;
}

.navigation {
  position: relative;
}

.navigation__list {
  position: relative;
  z-index: 5;
  display: none;
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .navigation__list {
    display: grid;
    font-size: 14px;
    line-height: 21px;
    box-shadow: none;
    grid-template-columns: repeat(5, 1fr);
    row-gap: 30px;
  }
}
@media (min-width: 1200px) {
  .navigation__list {
    grid-template-columns: repeat(5, 1fr);
  }
}

.navigation__item:last-child {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 87%, rgba(0, 0, 0, 0.2) 100%);
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .navigation__item:last-child {
    background: #ffffff;
  }
}

.navigation__item {
  padding-top: 14px;
  padding-bottom: 6px;
  background-color: #81b3d2;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__item {
    padding-top: 14px;
    padding-right: 5px;
    padding-bottom: 11px;
    padding-left: 5px;
    background-color: #ffffff;
    box-shadow: none;
  }
  .navigation__item:nth-child(3) {
    grid-column-start: 4;
  }
  .navigation__item--active .navigation__link {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .navigation__item {
    min-width: 240px;
    padding-top: 14px;
    padding-right: 0;
    padding-bottom: 11px;
    padding-left: 0;
  }
}

.navigation__link {
  display: block;
  margin: 0;
  padding: 5px;
  color: #ffffff;
  text-decoration: none;
}
@media (min-width: 768px) {
  .navigation__link {
    color: #000000;
  }
}

.navigation__item:hover {
  background-color: #669ec0;
}

.navigation__item:active {
  background-color: #5496bd;
}
.navigation__item:active .navigation__link {
  opacity: 0.3;
}

.navigation__item:disabled {
  background-color: #e5e5e5;
}
.navigation__item:disabled .navigation__link {
  opacity: 0.1;
}

@media (min-width: 768px) {
  .navigation__item:hover {
    background-color: transparent;
  }

  .navigation__item:hover a {
    color: #81b3d2;
  }

  .navigation__item:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }
}
